@import "https://fonts.googleapis.com/css2?family=Public+Sans:wght@100..900&display=swap";
:root {
  --section-width: 1110px;
  --section-padding: 24px;
  --green: #31c991;
  --dark: #2d314d;
  --white: #fff;
  --greyish--blue: #9698a6;
  --light-greyish-blue: #f3f4f6;
  --light-grey: #fafafa;
  --gradient: linear-gradient(to bottom right, #33d35e 0%, #2ab6d9 100%);
  --ff: "Public Sans", sans-serif;
  --fw-light: 300;
  --fw-medium: 400;
  --fw-bold: 700;
  --transition-1: .1s cubic-bezier(.47, 0, .745, .715);
  --transition-2: 50ms cubic-bezier(.47, 0, .745, .715);
  --transition-3: .15s cubic-bezier(.47, 0, .745, .715);
  --transition-4: .4s cubic-bezier(.075, .82, .165, 1);
  font-size: 16px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--light-grey);
  font-family: var(--ff);
}

.requestInviteCta {
  cursor: pointer;
  background-color: #0000;
  background-image: var(--gradient);
  font-family: var(--ff);
  font-weight: var(--fw-bold);
  color: var(--white);
  text-align: center;
  border: none;
  border-radius: 22px;
  width: 163px;
  height: 44px;
  font-size: .875rem;
  line-height: 1.75rem;
  position: relative;
}

.requestInviteCta:after {
  content: "";
  border-radius: inherit;
  opacity: 0;
  transition: var(--transition-1);
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media (hover: hover) and (pointer: fine) {
  .requestInviteCta:hover:after {
    opacity: .4;
  }
}

.logo {
  place-items: center;
  display: grid;
}

.section {
  max-width: var(--section-width);
  margin: auto;
}

.section--heading {
  font-weight: var(--fw-light);
  letter-spacing: -.57px;
  color: var(--dark);
  font-size: 32px;
}

.main {
  min-height: calc(100vh - 244px);
}

@media screen and (width >= 1100px) {
  .section--heading {
    letter-spacing: -.71px;
    font-size: 40px;
  }

  .main {
    min-height: calc(100vh - 260px);
  }
}
